import React from 'react';

export default class WordImg extends React.Component{
    constructor(){
        super()
        this.state={
            newsData:[{name:1,root:2}],
            content:[
                {item:'内容一'},
                {item:'内容二'},
                {item:'内容三'}
            ],
            showShare:false
        }

    }
    componentDidMount(){
        var newdateArr = "badiu"

        var _this = this;
        $.ajax({
            url:"/api/user/signup",

            type:"get",
            async:'false'
        }).done(function(data){

            console.log( $.type(data))

            _this.setState({newsData:data})
        })
    }
    showImg(event){
        this.setState()
    }
    render(){



        return(
            <div className="newsImgparent">
                {this.state.newsData.map(function (item,index) {
                    return(
                        <div key={index} className="newsImg">
                            <div>
                                 <img src={'public/css/img/'+item.img+".jpeg"}/>
                            </div>

                            <div className="fx1" >
                                <h2>{item.word}</h2>
                                <div onmouseenter={this.showImg.bind(this)} className="shareButton"></div>
                             </div>

                        </div>
                        )

                })}
            </div>
        )
    }
}


